.appeals-empty{
  padding: 12.5em 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__btn{
    height: 2.5em;
    margin-top: 1em;
    padding: 0 1.25em;
    border-radius: .5em;
    span{
      font-size: .875em;
    }
  }
}

.appeal-modal{
  .modal__dialog{
    width: 38.25em;
  }
}

.appeal-form{
  &__fields{
    @include grid100gap(1.25em);
    margin-top: 2.75em;
  }
  .fg-select{
    label{
      color: $black;
    }
  }
  &__contacts{
    &-wrap{
      margin-top: .5em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
  }
  &__bottom{
    width: 19.75em;
    max-width: 100%;
    margin: 1em auto 0;
    @include grid100gap(1.5em);
    .modal-form__btn{
      margin: 0;
    }
  }
  &__hint{
    text-align: center;
    font-size: .75em;
    line-height: 120%;
    color: $grayDark;
  }
}

.fg-select{
  .m-dropdown{
    &__show{
      background-color: transparent;
      border: $border;
    }
    &__current{
      font-size: .875em;
      font-weight: 400;
      color: $grayDark;
    }
    &__options{
      gap: 0;
    }
  }
  .m-check{
    input:hover~label,
    input:checked~label{
      color: $primary;
    }
    label{
      margin: 0;
      color: $black;
      min-height: 1.75em;
      display: flex;
      align-items: center;
      padding: .75em 0;
      &:before{
        display: none;
      }
      span{
        color: inherit;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .appeals-empty{
    padding: 10em 0 8.125em;
  }
}


@media screen and (max-width: 576px){
  .appeal-form{
    margin-top: 1.25em;
    &__contacts{
      &-wrap{
        grid-template-columns: 100%;
      }
    }
    &__bottom{
      gap: .5em;
    }
  }

  .fg-select{
    .gray-dropdown {
      .m-dropdown__show{
        height: 2.75em;
        border-radius: .5em;
        padding: 0 1.25em;
      }
    }
  }
}
