.header-middle{
  padding: 0 0 1.25em;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 13em;
    margin-right: 5.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.catalog-btn{
  height: 2.5em;
  padding: 0 1.25em;
  @include center;
  border-radius: .25em;
  margin-right: 1.25em;
  &__icon{
    width: 1.25em;
    height: 1.25em;
    flex-shrink: 0;
    position: relative;
    margin-right: .625em;
    img{
      width: 100%;
      display: block;
      @include centerAbsolute;
    }
  }
  &__title{
    font-size: 1em;
  }
}

.header-search{
  flex-grow: 1;
  margin-right: 1.25em;
  z-index: 2;
  position: relative;
  &._show-result{
    .header-search__clear,
    .search-modal{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__clear{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1.25em;
    width: 1em;
    @include center;
    opacity: 0;
    pointer-events: none;
    svg{
      width: 100%;
    }
  }
  .fg{
    z-index: 3;
    input{
      height: 2.86em;
      padding-left: 3.57em;
      border-radius: .4em;
    }
    label{
      top: 1em;
      left: 3.35em;
    }
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 1.25em;
    width: 1.25em;
  }
}

.search-modal{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  padding: 3.375em 1.75em 1.75em;
  background-color: #fff;
  box-shadow: $shadow;
  top: -.375em;
  left: -.375em;
  width: calc(100% + .75em);
  border-radius: .375em;
  &__group{
    padding: 1.125em 1.25em;
    border-bottom: $border;
    &:last-child{
      padding-bottom: 0;
      border-bottom: none;
    }
  }
  &__item{
    display: block;
    margin-bottom: .25em;
    &:last-child{
      margin-bottom: 0;
      .search-modal__item-desc{
        margin-bottom: 0;
      }
    }
    &-title{
      font-size: .9375em;
      line-height: 115%;
    }
    &-desc{
      font-size: .75em;
      line-height: 120%;
      color: $grayDark;
      display: block;
      margin-top: .25em;
      margin-bottom: 1em;
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__item{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: 2.5em;
    &:last-child{
      margin-right: 0;
    }
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  ._hide-desktop{
    display: none;
  }
}

.header-bottom{
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

.nav2{
  .menu{
    display: flex;
    justify-content: space-between;
    &>li{
      padding: 1em 0;
      @media screen and (min-width: 993px){
        .category-menu{
          opacity: 0;
          pointer-events: none;
          //transition: all .3s
        }
        &>a{
          cursor: pointer;
        }
        &._open{
          .category-menu{
            opacity: 1;
            pointer-events: unset;
          }
        }
      }
    }
    &__icon{
      display: none;
    }
  }
  a{
    font-size: .875em;
    letter-spacing: .01em;
  }
}

.category-menu{
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  margin-top: -3px;
  padding: 1.25em 0;
  background-color: $white;
  z-index: 10;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 17em) 15em;
    gap: 2em;
  }
  &__items{
    column-count: 2;
    column-gap: 1em;
    width: 46em;
    max-width: 100%;
    padding: 1.25em 0;
  }
  &__img{
    width: 100%;
    display: block;
  }
}

.category-group{
  margin-bottom: 2.5em;
  display: inline-block;
  width: 100%;
  &__title{
    font-size: 1em;
    line-height: 119%;
    font-weight: 700;
    display: inline;
  }
  &__count{
    font-size: .75em;
    line-height: 117%;
    color: $grayDark;
    display: inline;
  }
  &__links{
    margin-top: .875em;
    @include grid100gap(.375em);
    a{
      font-size: .875em;
      line-height: 120%;
    }
  }
}

.sub-menu-top__bg,
.sub-menu-top{
  display: none;
}


@media screen and (max-width: 992px){
  .header-middle{
    padding-bottom: 0;
    &__logo{
      width: 7.5em;
      margin: 0;
    }
  }

  .header-search,
  .catalog-btn{
    display: none;
  }

  .header-bottom{
    position: static;
  }

  ._open-sub-menu{
    .sub-menu-top{
      opacity: 1;
      pointer-events: unset;
      &__bg{
        display: block;
      }
    }
  }

  .nav2{
    .menu{
      padding: 0 1em;
      margin-bottom: 2.75em;
      @include grid100gap(.25em);
      &>li{
        padding: 0;
        &._open{
          .category-menu{
            opacity: 1;
            pointer-events: unset;
          }
        }
        &>a{
          display: flex;
          align-items: center;
          font-size: .8125em;
          &:after{
            content: '';
            width: 1.25em;
            height: 1.25em;
            background-image: url(../img/arrow.svg);
            @include bgImgSize(100%);
            margin-left: auto;
          }
        }
      }
      &__icon{
        margin-right: 1em;
        width: 2.625em;
        height: 2.625em;
        @include center;
        img{
          max-width: 100%;
          max-height: 100%;
        }
      }
    }
  }

  .category-menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: .3125em;
    right: 0;
    left: unset;
    width: calc(100% - .875em);
    box-shadow: $shadow;
    border-radius: 1.25em 0 0 1.25em;
    padding: 4.5em 1.5em .25em;
    z-index: 5;
    &__inner{
      display: block;
    }
    &__col2{
      display: none;
    }
    &__items{
      padding: 0;
    }
  }

  .category-group{
    margin-bottom: 1.25em;
  }

  .sub-menu-top{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    z-index: 12;
    right: 0;
    top: 0;
    height: 4em;
    width: calc(100% - .875em);
    display: flex;
    align-items: center;
    padding: 0 1em 0 1.5em;
    &__icon{
      margin-right: 1em;
      width: 2.125em;
      height: 2.125em;
      @include center;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    .close-btn{
      margin-left: auto;
    }
    &__bg{
      display: none;
    }
  }

  .header-action{
    position: fixed;
    z-index: 2;
    width: 100%;
    left: 0;
    bottom: 0;
    box-shadow: 0 -.3125em .875em rgba(0, 0, 0, 0.15);
    background-color: $white;
    padding: 1.125em;
    justify-content: space-between;
    &__item{
      width: 2em;
      height: 2em;
      background-color: $gray;
      border-radius: 50%;
      @include center;
      margin: 0 !important;
      svg{
        width: 1.125em;
      }
    }
    ._hide-mob{
      display: none;
    }
    ._hide-desktop{
      display: flex;
    }
    ._user{
      order: 2;
    }
  }
}


@media screen and (max-width: 576px){
  .category-menu{
    &__items{
      column-count: 1;
    }
  }

  .category-group{
    &__title{
      font-size: .8125em;
    }
    &__count{
      font-size: .625em;
    }
    &__links{
      gap: .125em;
      margin-top: .25em;
      a{
        font-size: .75em;
      }
    }
  }
}
