.portfolio-item{
  padding-bottom: 2.5em;
}

.portfolio{
  padding-bottom: 5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
  }
}

.portfolio-card2{
  &__img{
    clip-path: polygon(2.875em 0%, 100% 0, 100% 100%, 0 100%, 0% 2.875em);
    @include imgCover(51%);
    border-radius: 1.25em;
  }
  &__content{
    padding: .625em 1.875em 0;
    display: flex;
    justify-content: space-between;
  }
}

.req{
  margin: 0 0 -1.875em;
  padding: 4.375em 0 6.625em;
  background-size: cover;
  color: $white;
  .container{
    position: relative;
    z-index: 2;
  }
  &__inner{
    display: grid;
    grid-template-columns: 50.3125em 20em;
    justify-content: space-between;
  }
}

.fg{
  &._type2{
    input,textarea{
      background-color: $white;
      padding-top: 1.25em;
    }
    label{
      background-color: transparent;
    }
    input._filled~label,
    input:focus~label,
    select._filled~label,
    select:focus~label,
    textarea._filled~label,
    textarea:focus~label{
      top: .375em;
      transform: unset;
    }
  }
}

.req-form{
  &__fields{
    @include grid100gap(.9375em);
    margin-top: 2.25em;
  }
  &__btn{
    width: 100%;
    margin-top: 1.875em;
    border-radius: .5em;
    height: 2.5em;
    span{
      font-size: .875em;
    }
  }
  &__hint{
    margin-top: 1.25em;
    text-align: center;
    padding: 0 1.25em;
    color: $grayActive;
  }
}

.req-design{
  &__desc{
    margin-top: 2em;
    font-weight: 300;
    .text20{
      line-height: 140%;
    }
  }
  &__desc2{
    max-width: 29.375em;
    margin-top: 2.625em;
    .text16{
      line-height: 120%;
    }
  }
  &__desc3{
    max-width: 26.125em;
    margin-top: 3.125em;
    .text16{
      line-height: 120%;
    }
    span{
      -webkit-box-decoration-break: clone;
      box-decoration-break: clone;
      display: inline;
      background-color: $primary;
      padding: .2em;
      border-radius: .4em;
    }
  }
}


@media screen and (max-width: 992px){
  .req{
    padding: 2.25em 0;
    &__inner{
      grid-template-columns: calc(100% - 22.5em) 20em;
      gap: 2.5em;
    }
  }

  .req-design{
    &__top{
      flex-direction: column;
      align-items: flex-start;
      .m-link{
        margin-top: 1.25em;
        span{
          font-size: .875em;
        }
        svg{
          width: .875em;
          margin-left: .25em;
        }
      }
    }
    &__desc,
    &__desc2,
    &__desc3{
      margin-top: 1.25em;
    }
  }

  .req-form{
    &__btn,
    &__fields{
      margin-top: 1.25em;
    }
  }

  .portfolio{
    padding-bottom: 2.5em;
    .breadcrumbs{
      display: none;
    }
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: .875em;
    }
  }

  .portfolio-card2{
    &__img{
      border-radius: .5em;
      clip-path: polygon(1.25em 0,100% 0,100% 100%,0 100%,0 1.25em);
    }
    &__content{
      padding: .3125em 0 0 .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .req{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .portfolio{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
