.sales{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.875em 1.25em;
    grid-auto-flow: dense;
    margin-top: 3.75em;
  }
  .pagination{
    margin-top: 5em;
  }
}

.sale-card{
  &._large{
    grid-column: 1/-1;
    .sale-card__img{
      height: 20.625em;
    }
  }
  &._medium{
    grid-column: span 3;
  }
  &__img{
    display: block;
    border-radius: 1.25em;
    height: 16em;
    @include imgCover(100%);
    padding-bottom: 0;
  }
  &__content{
    margin-top: 1em;
    @include grid100gap(.625em);
  }
  &__title{
    @include truncate(2);
    line-height: 100%;
  }
  &__desc{
    @include truncate(3);
    line-height: 100%;
  }
}

.sale-item{
  margin-top: 1.375em;
  .sale-card__title{
    font-size: 1.3125em;
  }
}

.sale-products{
  margin-top: 3.75em;
  &__nav{
    margin-top: 1.875em;
  }
}

.sale-filter{
  display: flex;
  flex-wrap: wrap;
  margin-top: -.625em;
  .filter__all{
    margin: .625em .625em 0 0;
  }
}


@media screen and (max-width: 992px){
  .sales {
    padding-bottom: 1.875em;
    .breadcrumbs{
      display: none;
    }
    &__grid {
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1.25em;
    }
    .pagination{
      margin-top: 3.75em;
    }
  }

  .sale-card{
    &._medium{
      grid-column: 1/-1;
    }
  }

  .favourite__grid,
  .sale-products__grid{
    grid-template-columns: repeat(3,calc(100% / 3 - .625em / 3));
    gap: 1.25em .3125em;
    .product-card{
      &__img{
        padding-bottom: 91.5%;
      }
      &__content{
        padding: .625em .375em 0;
      }
      &__prices{
        display: block;
      }
      &__price2{
        margin: 0;
      }
      &__colors{
        display: none;
      }
    }
  }

  .favourite__nav,
  .sale-products__nav{
    .sort{
      position: relative;
      top: 0;
      order: -1;
      transform: unset;
    }
  }

  .sale-products{
    &__nav{
      margin-top: 1.25em;
    }
    &__grid{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .sales {
    &__grid {
      grid-template-columns: 100%;
    }
  }

  .sale-card{
    &._large{
      .sale-card__img{
        height: 8.5em;
      }
    }
    &__img{
      height: 14.75em;
    }
    &__content{
      margin-top: .625em;
      gap: .25em;
    }
    &__title{
      font-size: .8125em;
    }
    &__desc{
      font-size: .6875em;
    }
  }

  .favourite__grid,
  .sale-products__grid{
    grid-template-columns: repeat(2,calc(50% - .3125em / 2));
  }

  .sale-item{
    .sale-card__title{
      font-size: .8125em;
    }
    .sale-card__img{
      height: 9em;
    }
  }
}
