.footer{
  background-color: $black;
  color: $white;
  padding: 3.75em 0 .75em;
  margin-top: 1.875em;
  &:after{
    background: rgba(#000,.2);
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}

.footer-nav{
  ul{
    @include grid100gap(1.25em);
  }
  a{
    font-size: 1.0625em;
    line-height: 117%;
  }
}

.footer-contacts{
  margin-top: -1.25em;
  text-align: right;
  &__phone{
    font-size: 2.25em;
    line-height: 119%;
    font-weight: 100;
  }
  &__groups{
    margin-top: 3.125em;
    @include grid100gap(3.125em);
  }
  &__group{
    @include grid100gap(.3125em);
    position: relative;
    &:last-child{
      &:after{
        display: none;
      }
    }
    &:after{
      content: '';
      position: absolute;
      background-image: url(../img/lines.svg);
      width: 1.125em;
      height: .1875em;
      @include bgImgSize(100%);
      right: 0;
      top: 100%;
      margin-top: 1.5625em;
      transform: translateY(-50%);
    }
  }
  &__item{
    font-size: 1em;
    line-height: 125%;
  }
  .socials{
    justify-content: flex-end;
    margin-top: 7em;
  }
  &__c{
    margin-top: 4.875em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    font-size: .75em;
    padding-bottom: 7em;
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 2.5em 0 7.5em;
    &__inner{
      @include grid100gap(3.5em);
    }
  }

  .footer-contacts{
    margin-top: 1.25em;
    &__phone{
      font-size: 1.75em;
    }
    &__groups{
      margin-top: 1.75em;
    }
    &__address{
      max-width: 13.75em;
      margin-left: auto;
    }
    .socials{
      margin-top: 4em;
    }
    &__c{
      margin-top: 3.125em;
    }
  }
}
