.order-form{
  margin-top: 3.5em;
  &__grid{
    margin-top: 2.25em;
  }
  &__check{
    display: flex;
    padding: 1.25em 0 0 1.25em;
  }
}

.order-requisites{
  margin-top: 1.875em;
  &__view{
    display: none;
    &._active{
      display: block;
    }
  }
}

.requisites-form{
  width: calc((100% / 3 - 1.75em / 3) * 2 + .875em);
  &__fields{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 4.375em / 6));
    gap: .875em;
    margin-top: 1.75em;
    ._col2{
      grid-column: span 2;
    }
    ._col3{
      grid-column: span 3;
    }
  }
  &__wrap{
    grid-column: 1/-1;
    display: flex;
    align-items: center;
    .fg{
      flex-grow: 1;
    }
    .m-check{
      margin-left: .875em;
      flex-shrink: 0;
      label{
        align-items: center;
        padding-top: 0;
        &:before{
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
  }
}

.hint-modal{
  .modal__dialog{
    width: 67.625em;
    padding: 3.5em 3.75em 3.875em;
  }
  .close-btn{
    top: 2.25em;
    right: 2.625em;
  }
  .modal__title{
    text-transform: uppercase;
  }
  &__desc{
    margin-top: 1.875em;
    max-width: 52.25em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.875em));
    gap: 3.75em;
    margin-top: 1.75em;
  }
  .delivery-item__group{
    .m-link{
      margin-top: 1.5em;
      display: inline-block;
    }
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 1.25em;
  }
}

.radio-field{

}

.delivery-type{
  display: none;
  &._active{
    display: block;
  }
  ._cart-grid{
    margin-top: 1.5em;
  }
}

.gray-dropdown{
  .m-dropdown__show{
    padding: 0 1.25em;
    background-color: #F7F7F7;
    height: 3.375em;
    border-radius: .625em;
    font-weight: 700;
    justify-content: space-between;
    z-index: 7;
    position: relative;
    transition: all .3s;
    color: $black;
    &:after{
      background-image: url(../img/select-arrow.svg);
      width: 1.25em;
      height: 1.25em;
    }
  }
  .m-dropdown__hidden{
    left: 0;
    min-width: 100%;
    width: 100%;
    top: 0;
    margin: 0;
    padding-top: 3.375em;
  }
  .m-dropdown__options{
    gap: .625em;
    max-height: 15.5em;
    @include scroll;
    overflow-y: auto;
  }
  &._open{
    .m-dropdown__show{
      border-color: transparent;
      background-color: transparent;
    }
  }
}

.delivery-target{
  .radio-field{
    min-height: 3.375em;
  }
  .hint{
    top: .625em;
    right: .625em;
  }
}

.floor-dropdown{
  .m-dropdown__options{
    padding-left: 1.25em;
    width: calc(100% + 1em);
  }
}

._user-type-fields{
  display: none;
  &._active{
    display: block;
  }
}

.transport-co{
  padding: .75em;
  border-radius: .625em;
  background-color: #F7F7F7;
  margin-top: 1.25em;
  &__top{
    text-align: center;
    margin: 0 auto .625em;
    width: 43em;
    max-width: 100%;
  }
  &__fields{
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
    width: 39.125em;
    max-width: 100%;
  }
  .fg{
    input{
      background-color: #fff;
    }
  }
}


@media screen and (max-width: 992px){
  .requisites-form{
    width: 100%;
  }

  .hint-modal{
    .modal__dialog{
      padding: 2em 1em;
    }
    .close-btn{
      right: 0;
      top: unset;
      bottom: 100%;
      margin-bottom: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .delivery-tariff{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
    .radio-field{
      min-height: 2.75em;
      b{
        display: block;
      }
      .hint{
        top: 0;
        right: 0;
        &__btn{
          circle{
            fill: transparent;
          }
        }
      }
    }
  }

  .delivery-type{
    ._cart-grid{
      margin-top: 1em;
    }
    .gray-dropdown{
      grid-column: 1/-1;
    }
  }

  .delivery-target{
    grid-template-columns: 100%;
    gap: .625em;
    .radio-field{
      min-height: 2.25em;
      align-items: center;
      .hint{
        top: .5em;
      }
    }
  }

  .gray-dropdown{
    .m-dropdown__show{
      padding: 0 .75em;
      height: 2.25em;
    }
    .m-dropdown__hidden{
      padding-top: 2.5em;
    }
    .m-dropdown__current{
      font-size: .75em;
    }
  }

  .requisites-form{
    &__fields{
      grid-template-columns: 100%;
      gap: .5em;
      ._col2,
      ._col3{
        grid-column: 1/-1;
      }
    }
    &__wrap{
      display: block;
      .m-check{
        margin: .375em 0 0;
        br{
          display: none;
        }
      }
    }
  }

  .order-form{
    margin-top: 2.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: .5em;
    }
    &__check{
      font-size: .75em;
      max-width: 90%;
      padding: 1em 0 0 1em;
      label{
        padding-top: 0 !important;
        &:before{
          font-size: .875em;
        }
      }
    }
  }

  .transport-co{
    &__fields{
      grid-template-columns: 100%;
    }
  }

  .hint-modal{
    &__grid{
      grid-template-columns: 100%;
      gap: .75em;
      &._mob-gap20{
        gap: 1.25em;
      }
    }
    .delivery-item__group{
      .m-link{
        margin-top: 1em;
      }
    }
    &__desc{
      margin-top: .875em;
      text-align: center;
    }
  }
}
