._cart-grid{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1.75em / 3));
  gap: .875em;
}

._cart-grid2{
  grid-template-columns: repeat(4,calc(25% - 2.625em / 4));
}

.cart-delivery__item,
.radio-field{
  .hint{
    top: 1.25em;
    right: 1.25em;
    position: absolute;
    z-index: 2;
  }
}

.cart-delivery{
  &__item{
    input:hover,
    input:checked{
      &~.delivery-tab{
        color: $primary;
        border-color: $primary;
      }
    }
  }
}

.cart-map{
  margin-top: 1.875em;
  position: relative;
  &__search{
    position: absolute;
    top: 1.25em;
    left: 1.25em;
    width: calc(100% - 2.5em);
    z-index: 2;
    &._show{
      .cart-map__search-hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    .fg{
      position: relative;
      z-index: 2;
      input{
        background-color: $white;
        padding-left: 9.71em;
      }
      label{
        pointer-events: none;
        position: absolute;
        left: 1.43em;
        top: 50%;
        transform: translateY(-50%);
        span{
          color: inherit;
        }
      }
    }
    &-hidden{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      padding: 4em 1em 1.875em 8.5em;
      background-color: $white;
      border-radius: .5em;
      border: $border;
      transition: all .3s;
      pointer-events: none;
      opacity: 0;
    }
    &-result{
      @include grid100gap(.5em);
    }
    &-item{
      font-size: .875em;
      line-height: 130%;
      &:hover{
        color: $primary;
      }
    }
  }
  &__hint{
    padding: 0 0 1.875em 2.5em;
    br{
      display: none;
    }
    p{
      line-height: inherit;
    }
  }
}

.delivery-tariff{
  margin-top: 1.875em;
  &__view{
    display: none;
    margin-top: 1.0625em;
    &._active{
      display: block;
    }
  }
  &__hint{
    padding: 1em 1.25em;
    border-radius: .625em;
    background-color: $primary;
    color: $white;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 8.875em;
      margin-top: -.5em;
      background-image: url(../img/triangle.svg);
      width: 1.5em;
      height: .75em;
      @include bgImgSize(100%);
    }
    &._type2{
      &:before{
        left: calc(100% / 3 - 1.75em / 3 + 8.875em + .875em);
      }
    }
    &._type3{
      &:before{
        left: unset;
        right: 8.875em;
      }
    }
  }
  .hint__btn{
    circle{
      fill: $grayActive;
    }
  }
  .radio-field{
    min-height: 3.375em;
  }
}

.radio-field{
  padding: .75em 1.25em;
  min-height: 4.375em;
  display: flex;
  align-items: center;
  border-radius: .625em;
  background-color: $gray;
  position: relative;
  &__check{
    width: 1.5em;
    height: 1.5em;
    flex-shrink: 0;
    border-radius: .4375em;
    border: $border;
    background-image: url(../img/check7.svg);
    @include bgImgSize(0);
    margin-right: .6875em;
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 1px solid $primary;
      border-radius: .625em;
      opacity: 0;
      pointer-events: none;
    }
  }
  &__icon{
    width: 1.375em;
    margin-right: .625em;
    &._big{
      width: 1.75em;
    }
  }
  &__title{
    line-height: 120%;
  }
  input:checked{
    &~.radio-field__check{
      background-size: .6875em auto;
      background-color: $primary;
      border-color: $primary;
      &:after{
        opacity: 1;
      }
    }
  }
}

.delivery-service{
  margin-top: 3.125em;
  &__items{
    margin-top: 1.25em;
  }
  &__field{
    margin-top: 1.875em;
  }
  .radio-field{
    min-height: 3.375em;
    .hint{
      top: .625em;
      right: .625em;
    }
  }
}

.user-type{
  .radio-field__icon{
    width: 2.5em;
  }
  .radio-field{
    &__check{
      width: 0;
      height: 0;
      border: none;
      margin: 0;
    }
    input:checked{
      &~.radio-field__title{
        color: $primary;
      }
    }
  }
  &__hint{
    padding: .75em 0 0 .6875em;
    display: flex;
    align-items: center;
    color: $grayDark;
    a{
      text-decoration: underline;
    }
    img{
      width: .625em;
      margin-right: .5375em;
    }
  }
}

.requisites-upload{
  .file-input{
    &__btn{
      width: 16.5em;
      background-color: $white;
      box-shadow: $shadow;
      color: $grayDark;
    }
  }
}


@media screen and (max-width: 992px){
  .cart-map{
    &__search{
      width: calc(100% - 1.25em);
      left: .625em;
      top: .75em;
      &-hidden{
        padding-left: 1em;
      }
      .fg{
        label{
          span{
            display: none;
          }
        }
        input{
          padding-left: 4.75em;
        }
      }
    }
    &__hint{
      padding: 0 0 1.25em;
      br{
        display: unset;
      }
      p{
        margin-bottom: 1.43em;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .cart-delivery{
    grid-template-columns: repeat(3,calc(100% / 3 - .125em));
    gap: .1875em;
  }

  .cart-delivery__item .hint,
  .radio-field .hint{
    top: .625em;
    right: .625em;
  }

  .cart-map{
    .map{
      height: 18.75em;
    }
  }

  .radio-field{
    padding: .375em .875em;
    align-items: flex-start;
    min-height: 2.75em;
    &__check{
      font-size: .67em;
    }
    &__title{
      font-size: .75em;
    }
    &__icon{
      &._big{
        width: 1.5em;
      }
    }
  }

  .delivery-tariff{
    &__view{
      margin-top: .875em;
      &._no-triangle-mob{
        .delivery-tariff__hint:before{
          display: none;
        }
      }
    }
    &__hint{
      padding: 1em 1.75em;
      font-size: .6875em;
      border-radius: 1em;
      &:before{
        left: 5.45em;
        width: 2.2em;
        height: 1.1em;
        margin-top: -.7em;
      }
      .text16{
        display: block;
      }
      &._type2{
        &:before{
          left: unset;
          right: 5.45em;
        }
      }
    }
  }

  .delivery-service{
    margin-top: 1.875em;
    &__items{
      grid-template-columns: 100%;
      gap: .625em;
      margin-top: 1em;
      &._col2-mob{
        grid-template-columns: repeat(2,calc(50% - .3125em));
      }
    }
    .radio-field{
      min-height: 2.25em;
      align-items: center;
      .hint{
        top: .5em;
      }
    }
    &__field{
      margin-top: .625em;
    }
  }

  .user-type{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .1875em / 2));
      gap: .1875em;
    }
    .radio-field{
      flex-direction: column;
      align-items: center;
      padding: .75em;
      border-radius: .75em;
      &__icon{
        margin: 0 0 .1875em;
        width: auto;
        height: 1.875em;
      }
    }
    &__hint{
      padding-left: 0;
      align-items: flex-start;
      .text14{
        font-size: .625em;
      }
    }
  }
}

