.header{
  position: relative;
  padding-bottom: 3em;
  z-index: 10;
  &__inner{
    display: flex;
  }
  &__logo{

  }
}

.header-top{
  padding: 1em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__phone{
    margin-left: 2.25em;
    font-weight: 600;
  }
  &__call{
    margin-left: 1.75em;
    display: flex;
    align-items: center;
    color: $primary;
    svg{
      width: .875em;
      margin-right: .25em;
    }
    span{
      text-decoration: underline;
    }
    &:hover{
      color: $black;
      path{
        fill: $black;
      }
    }
  }
  &__time{
    margin-left: 2em;
    font-weight: 600;
    color: $grayDark;
  }
}

.header-city__hidden,
.city-modal{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  left: -1.5em;
  margin-top: .625em;
  background-color: $white;
  box-shadow: $shadow;
  border-radius: 1.25em;
}

.header-city{
  position: relative;
  z-index: 3;
  &._open{
    .header-city__show{
      color: $primary;
      svg{
        transform: rotate(-180deg);
      }
      path{
        fill: $primary;
      }
    }
    .header-city__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._show-modal{
    .city-modal{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    transition: all .3s;
    display: flex;
    align-items: center;
    color: $grayDark;
    font-weight: 600;
    &:hover{
      cursor: pointer;
      color: $primary;
      path{
        fill: $primary;
      }
    }
    span{
      font-size: .875em;
    }
    svg{
      width: 1.25em;
      margin-left: .25em;
    }
  }
  &__hidden{
    width: 40.75em;
    padding: 2em 1.75em;
  }
  &__search{
    input{
      height: 2.86em;
      padding-left: 3.57em;
    }
    label{
      top: 1em;
      left: 3.35em;
    }
    &-icon{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 1.25em;
      width: 1em;
    }
  }
  &__items{
    padding: 2em 1.25em 0;
    column-count: 3;
    column-gap: 1em;
  }
  &__item{
    display: inline-block;
    width: 100%;
    font-size: 1em;
    line-height: 130%;
    margin-bottom: .5em;
    &._active{
      font-weight: 700;
    }
  }
}

.city-modal{
  padding: 1em 2em;
  width: 16em;
  text-align: center;
  border-radius: .625em;
  position: fixed;
  left: 50%;
  top: 50%;
  margin: 0;
  transform: translate(-50%,-50%);
  &__nav{
    margin-top: .75em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5625em));
    gap: 1.125em;
  }
  &__btn{
    height: 2.5em;
    padding: 0;
    span{
      font-size: 1em;
    }
  }
  &__close{
    position: absolute;
    width: 1em;
    @include center;
    top: .75em;
    right: 1em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.nav{
  margin-left: auto;
  color: $grayDark;
  .menu{
    display: flex;
    align-items: center;
  }
  li{
    margin-left: 1.25em;
  }
  a{
    font-weight: 600;
    font-size: .875em;
  }
}

.burger{
  display: none;
}

.header-mob-action{
  display: none;
}

.menu-top{
  display: none;
}

.header-contacts{
  display: flex;
  align-items: center;
}



@media screen and (max-width: 992px){
  .header{
    //display: none;
    padding: 1.375em 0 1.75em;
    &._open{
      .header-top{
        transform: translateX(0);
      }
    }
  }

  .header-top{
    z-index: 10;
    transition: all .5s;
    transform: translateX(-150%);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #fff;
    padding: 0;
    .container{
      padding: 0;
    }
    &__inner{
      height: $vh;
      overflow-y: auto;
      flex-direction: column;
    }
    &__phone{
      margin: .625em 0 0;
    }
    &__call{
      margin: .625em 0 0;
      order: 2;
    }
    &__time{
      margin: .125em 0 0;
    }
  }

  .burger{
    @include center;
    width: 2.8125em;
    height: 1.875em;
    border-radius: .1875em;
    margin-right: .75em;
    img{
      width: 1em;
    }
  }

  .header-mob-action{
    display: flex;
    align-items: center;
    margin-left: auto;
    &__btn{
      width: 1.0625em;
      @include center;
      margin-left: 1.75em;
      svg{
        width: 100%;
      }
    }
  }

  .menu-top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 4em;
    padding: 0 1em;
    order: 1;
    flex-shrink: 0;
  }

  .nav{
    order: 3;
    padding: .625em 1em 1.875em;
    margin: 0;
    width: 100%;
    .menu{
      @include grid100gap(.625em);
      li{
        margin: 0;
      }
    }
  }

  .header-bottom{
    order: 2;
  }

  .header-contacts{
    width: 100%;
    order: 4;
    flex-direction: column;
    padding: 1.875em;
    background-color: $gray;
    margin-top: auto;
  }

  .header-city{
    &__hidden{
      width: calc(100vw - 1em);
      left: 50%;
      transform: translateX(-50%);
      top: unset;
      bottom: 100%;
      margin: 0 0 .75em;
      padding: 1em;
      border-radius: 1em;
    }
    &__items{
      padding: 1em 0 0;
    }
    &__item{
      font-size: .875em;
    }
  }

  .city-modal{
    display: none;
  }
}


@media screen and (max-width: 576px) {
  .header-city{
    &__items{
      column-count: 1;
    }
  }
}
