.orders{
  @include grid100gap(1.875em);
  margin-top: 1.875em;
}

.orders-group{
  padding: 1.875em;
  border-radius: .625em;
  box-shadow: $shadow;
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__title{
    font-size: 1.125em;
    line-height: 122%;
    letter-spacing: .01em;
    font-weight: 700;
  }
  &__date{
    font-size: .875em;
    color: $grayDark;
    margin-left: .9em;
    margin-bottom: .25em;
  }
}

.order-item{
  display: grid;
  grid-template-columns: 19.75em auto auto;
  gap: 1.25em;
  align-items: center;
  margin-top: 1.875em;
  border-top: $border;
  padding-top: 1.875em;
  &:first-child{
    padding-top: 0;
    border-top: none;
  }
  &__img{
    @include imgCover(59%);
  }
  &__desc{
    letter-spacing: .01em;
    margin-top: .25em;
  }
  &__price{
    text-align: right;
  }
}


@media screen and (max-width: 992px){
  .orders{
    gap: 3.25em;
  }

  .orders-group{
    padding: .625em;
  }

  .order-item{
    grid-template-columns: 15em auto auto;
    margin-top: 1.25em;
    padding-top: 1.25em;
    &__desc{
      margin-top: .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .orders-group{
    &__top{
      display: block;
    }
    &__date{
      margin: .125em 0 0;
    }
  }

  .order-item{
    grid-template-columns: 100%;
    gap: .75em;
    &__price{
      text-align: left;
    }
  }
}
