.collection-slider{
  margin-top: 1.875em;
  position: relative;
  @media screen and (min-width: 993px){
    &__container{
      width: calc(100% + 2em);
    }
    &__slide{
      width: 25%;
      padding-right: 2em;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      &.swiper-slide-active,
      &.swiper-slide-next,
      &.swiper-slide-next+.swiper-slide,
      &.swiper-slide-next+.swiper-slide+.swiper-slide{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__nav{
      .swiper{
        &-button{
          top: 5.8125em;
          &-prev{
            left: -3.375em;
          }
          &-next{
            right: -3.375em;
          }
          @media screen and (max-width: 1400px){
            &-prev{
              left: -2.375em;
            }
            &-next{
              right: -2.375em;
            }
          }
        }
      }
    }
  }
}

.filter-modal{
  padding: 0;
  &:after{
    display: none;
  }
  .modal{
    &__dialog{
      width: 100%;
      max-width: unset;
      border-radius: 0;
      height: $vh;
      overflow-y: auto;
      padding: 3.5em 0;
    }
  }
  .close-btn{
    top: -.25em;
    right: 0;
  }
  &__inner{
    position: relative;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 2em));
    column-count: 5;
    column-gap: 2.5em;
    margin-top: 3.75em;
  }
  .filter2-group{
    display: inline-block;
    margin-bottom: 1.875em;
    width: 100%;
    padding-top: 1em;
    &__top{
      pointer-events: none;
    }
    &__hidden{
      display: block !important;
    }
    &__checks{
      padding: 0;
    }
    &__more{
      margin-left: 0;
    }
  }
  &__nav{
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }
  &__btn{
    margin-bottom: .625em;
    ._view2{
      display: none;
    }
  }
  &--show-desktop{
    @media screen and (min-width: 993px){
      opacity: 1;
      pointer-events: unset;
      position: static;
      height: unset;
      .modal__bg,
      .h1,
      .close-btn{
        display: none;
      }
      .modal__dialog{
        width: 100%;
        background-color: transparent;
        padding: 0;
        position: static;
        height: unset;
      }
      .container{
        padding: 0;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .ceo{
    .text{
      text-align: left;
    }
  }

  .collection-slider{
    margin-top: 1.25em;
    .swiper-slide{
      width: 15.625em;
      padding-right: .625em;
    }
    &__nav{
      .swiper-button{
        display: none;
      }
    }
  }

  .filter-modal{
    .h1{
      text-align: center;
    }
    .modal__dialog{
      padding: 2.25em 1em;
    }
    .close-btn{
      top: 1.125em;
      right: .875em;
    }
    .container{
      padding: 0;
    }
    &__inner{
      position: static;
    }
    &__grid{
      margin-top: 1.625em;
      grid-template-columns: 100%;
      gap: .625em;
    }
    .filter2-group{
      margin-bottom: .625em;
      &:last-child{
        margin-bottom: 0;
      }
    }
    &__nav{
      margin-top: 1.625em;
      flex-direction: row;
      justify-content: center;
    }
    &__btn{
      margin: 0 .625em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}


