.compare{
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 15em calc(100% - 15.625em);
    gap: .625em;
  }
}

.compare-slider{
  .swiper{
    &-container{
      width: calc(100% + .625em);
    }
    &-slide{
      width: 25%;
      padding-right: .625em;
      &:nth-child(1),
      &-active{
        .compare-card__row{
          border-radius: .3125em 0 0 .3125em;
        }
      }
      &:last-child,
      &-next+.swiper-slide+.swiper-slide{
        .compare-card__row{
          border-radius: 0 .3125em .3125em 0;
        }
      }
    }
    &-pagination{
      display: none;
    }
  }
}

.compare-card{
  &__top{
    height: 21.875em;
  }
  &__empty{
    height: calc(100% - 2.5em);
    background-color: $gray;
    @include center;
    flex-direction: column;
    border-radius: .625em;
    text-align: center;
    color: $grayActive;
    &-btn{
      @include center;
      border-radius: 50%;
      width: 2.5em;
      height: 2.5em;
      border: 1px solid #BCBCBC;
      color: $grayActive;
      margin-bottom: 1.125em;
    }
  }
  .product-card{
    &:before{
      display: none;
    }
    &__content{
      padding-right: .625em;
      padding-bottom: 1.25em;
    }
    &__title{
      @include truncate(3);
    }
  }
  .cart-btn{
    transform: translateY(-100%);
  }
  &__row{
    height: 2.375em;
    display: flex;
    align-items: center;
    padding: 0 1.25em;
    width: calc(100% + .625em);
    &:nth-child(odd){
      height: 2.75em;
      background-color: $gray;
    }
  }
}

.compare-sidebar{
  .compare-card__row{
    text-transform: uppercase;
    font-weight: 700;
    padding-left: 0;
    &:nth-child(odd){
      background-color: transparent;
    }
    span{
      font-size: .875em;
      line-height: 121%;
    }
  }
}

.compare-nav{
  margin-top: 2em;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  &__bottom{
    width: 100%;
    margin-top: .625em;
    display: flex;
    align-items: center;
  }
  &__items{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__clear-wrap,
  .selected-filters__item{
    margin: .625em .625em 0 0;
  }
  &__clear{
    display: flex;
    align-items: center;
    padding: 0 1.25em;
    height: 1.875em;
    color: $primary;
    span{
      font-size: .875em;
    }
  }
  .m-check{
    label{
      display: flex;
      align-items: center;
      padding-left: 1.875em;
      min-height: 1.375em;
      &:before{
        width: 1.375em;
        height: 1.375em;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    input:checked~label:before{
      background-size: .75em auto;
    }
  }
}


@media screen and (max-width: 992px){
  .compare{
    .breadcrumbs{
      display: none;
    }
    &__grid{
      grid-template-columns: 9.375em calc(100% - 10em);
    }
  }

  .compare-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .compare-sidebar{
    .compare-card__row{
      span{
        font-size: .75em;
      }
    }
  }

  .compare-card{
    &__top{
      height: 20em;
    }
    &__row{
      padding: 0 .625em;
      &._big{
        height: 5em;
      }
    }
  }

  .compare-nav{
    display: block;
    &__items{
      position: relative;
      width: calc(100% + 1.25em);
      padding: 0 .625em;
      margin: 1.875em 0 0 -.625em;
      flex-wrap: nowrap;
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
      .selected-filters__item{
        flex-shrink: 0;
        margin-top: 0;
      }
    }
    &__bottom{
      display: block;
    }
    &__clear{
      height: 1.375em;
      position: sticky;
      left: 0;
      &-wrap{
        width: 100%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .compare{
    overflow: hidden;
    &__grid{
      position: relative;
    }
    &__col2{
      position: relative;
      z-index: 2;
    }
  }

  .compare-slider{
    position: static;
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 100%;
        background-color: #fff;
      }
      &-pagination{
        display: flex;
        justify-content: center;
        position: absolute;
        top: 18em;
        left: unset;
        right: 0;
        width: calc(100vw - 1.25em);
        margin: 0;
      }
    }
  }

  .compare-card{
    &__top{
      height: 20em;
    }
    .product-card{
      &__img{
        padding-bottom: 91.5%;
      }
      &__content{
        padding: .625em .375em;
      }
      &__prices{
        display: block;
        margin-top: .625em;
      }
      &__price2{
        margin-left: 0;
      }
      .cart-btn{
        transform: unset;
      }
    }
  }
}
