.custom-main{
  padding: 4.375em 0 8.75em;
  margin-bottom: 3.75em;
  min-height: 25em;
  background-size: cover;
  background-position: center;
  color: $white;
  position: relative;
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    &:after{
      background: #000;
      opacity: 0.5;
    }
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__inner{
    width: 48.25em;
    max-width: 100%;
    position: relative;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 139%;
    font-weight: 300;
    margin-top: .89em;
  }
  &__nav{
    display: flex;
    margin-top: 1em;
  }
  &__play{
    position: absolute;
    left: 100%;
    top: 4.25em;
    margin-left: 3.625em;
    width: 3.3125em;
    height: 3.3125em;
    background-color: #d9d9d9;
    border-radius: 50%;
    @include center;
    &:hover{
      background-color: $primary;
    }
    img{
      width: 1.0625em;
    }
  }
}

.m-section2{
  padding: 2.5em 0;
}

.custom{
  &__top{
    @include grid100gap(1.875em);
    text-align: center;
  }
  &__desc{
    max-width: 42.9em;
    margin: 0 auto;
    font-weight: 300;
    font-size: 1.125em;
    line-height: 139%;
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.875em 1.25em;
  }
  &__text{
    margin-top: 1.875em;
    column-count: 2;
    column-gap: 3.125em;
    padding: 0 2.125em;
    p{
      display: inline-block;
      width: 100%;
      font-size: .875em;
      line-height: 130%;
      margin: 0 0 1.25em !important;
    }
  }
}

.custom-card{
  position: relative;
  color: $white;
  display: block;
  &:hover{
    color: $white;
    .custom-card__img:after{
      opacity: .6;
    }
  }
  &__img{
    clip-path: polygon(2.875em 0%, 100% 0, 100% 100%, 0 100%, 0% 2.875em);
    @include imgCover(77.2%);
    border-radius: 1.25em;
    &:after{
      background: #000;
      opacity: 0.2;
      transition: all .3s;
    }
  }
  &__content{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 1.875em 1.625em;
  }
  &__desc{
    margin-top: 1em;
    font-size: 1em;
    line-height: 120%;
    font-weight: 500;
    @include truncate(3);
  }
}


@media screen and (max-width: 992px){
  .m-section2{
    padding: 1.875em 0;
  }

  .custom-main{
    margin-bottom: 0;
    color: $black;
    padding: 2.375em 0 1.875em;
    min-height: unset;
    &__bg{
      position: static;
      height: auto;
      margin-bottom: 1.25em;
      &:after{
        display: none;
      }
    }
    &__inner,
    .container{
      position: static;
    }
    .h1{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      text-align: center;
    }
    &__desc{
      font-size: .8125em;
      text-align: center;
    }
    &__nav{
      margin-top: .625em;
      align-items: center;
      .m-btn-white-outline{
        border-color: $black;
        color: $black;
        height: 2.75em;
        padding: 0 1.25em;
        &:hover{
          background-color: $black;
          color: #fff;
        }
        span{
          font-size: 1.1875em;
        }
      }
    }
    &__play{
      position: static;
      margin-left: 3.125em;
    }
  }

  .custom{
    &__top{
      gap: .625em;
    }
    &__desc{
      font-size: .8125em;
    }
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    &__text{
      padding: 0;
      margin-top: 1.25em;
    }
  }

  .custom-card{
    &__content{
      font-size: 1.5em;
      padding: 1.25em 1.125em;
    }
    &__title{
      font-size: .75em;
    }
    &__desc{
      font-size: .6875em;
    }
  }
}


@media screen and (max-width: 576px){
  .custom{
    &__grid{
      grid-template-columns: 100%;
    }
    &__text{
      column-count: 1;
    }
  }

  .custom-card{
    &__content{
      font-size: 1em;
    }
  }
}
