.about4{
  .about{
    padding: 4em 0;
    margin: 1.875em 0 0;
    &__items{
      margin: 0;
    }
  }
  &__top{
    text-align: center;
    width: 46.625em;
    max-width: 100%;
    margin: 0 auto 1.875em;
    @include grid100gap(1.875em);
  }
}

.welcome{
  padding: 10em 0;
  background-size: cover;
  background-position: center;
  margin-bottom: .3125em;
  &__inner{
    width: 26.5em;
    max-width: 100%;
    text-align: center;
    margin: 0 auto;
    .h1{
      font-weight: 900;
    }
  }
}

.cooperate{
  &__inner{
    padding: 2.75em;
    background-image: url(../img/cooperate-bg.webp);
    background-size: cover;
    background-position: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $gray;
    border-radius: 1.875em;
  }
  &__desc{
    width: 39.5em;
    max-width: 100%;
  }
  &__btn{
    margin-top: 1.625em;
  }
}

.assortment{
  margin: 2.5em 0;
  background-size: cover;
  background-position: center;
  padding: 3.75em 0;
  color: $white;
  min-height: 37.5em;
  &:after{
    background-color: rgba($black,.8);
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 11.25em / 4));
    gap: 3.75em;
    margin-top: 3.75em;
  }
  &__subtitle{
    margin: 10em auto 0;
    width: 54em;
    max-width: 100%;
    text-align: center;
    font-weight: 100;
    span{
      font-size: 3.5em;
    }
  }
}

.assortment-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &:hover{
    .assortment-item__btn{
      opacity: 1;
    }
  }
  &__num{
    font-size: 3.5em;
    font-weight: 100;
  }
  &__text{
    min-height: 3.125em;
    @include center;
    margin-top: 1em;
  }
  &__btn{
    opacity: 0;
    height: 2.375em;
    margin-top: 1.25em;
    span{
      font-size: .875em;
    }
  }
}

.opinion{
  &__top{
    @include grid100gap(1.875em);
  }
  &__items{
    margin-top: 2.5em;
    display: flex;
    justify-content: space-between;
  }
}

.rating-block{
  display: flex;
  align-items: center;
  &__num{
    display: flex;
    align-items: center;
    order: -1;
    font-size: 1.8125em;
    font-weight: 800;
    margin-right: .33em;
  }
}

.rating-block{
  &._disabled{
    pointer-events: none;
  }
}

.rating{
  cursor: pointer;
  &__stars{
    width: 8.25em;
    max-width: 100%;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__fill{
    width: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    overflow: hidden;
    img{
      width: 8.25em;
      display: block;
    }
  }
}

.site-item{
  &__img{
    height: 1.625em;
    display: flex;
    align-items: center;
    margin-bottom: .5em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.assortment-projects{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  gap: 1.5em;
  margin-top: 4.5em;
}

.assortment-item2{
  text-align: center;
  &__top{

  }
  &__title{
    font-size: 3.5em;
    text-transform: uppercase;
    font-weight: 100;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 133%;
    font-weight: 500;
  }
  &__projects{
    margin-top: 5em;
    @include grid100gap(1.5em);
  }
  &__project{
    font-size: 1.375em;
    font-weight: 300;
    text-transform: uppercase;
  }
}


@media screen and (max-width: 992px){
  .welcome{
    margin-top: 2.375em;
    margin-bottom: 0;
    position: relative;
    padding-top: 0;
    padding-bottom: 21%;
    &__inner{
      position: absolute;
      bottom: 100%;
      left: 0;
      width: 100%;
      min-height: 2.375em;
    }
  }

  .about4{
    padding-top: 1.25em;
    &__top{
      gap: .625em;
    }
  }

  .cooperate{
    .container{
      padding: 0;
    }
    &__inner{
      padding: 1.875em 1em;
      border-radius: 0;
    }
    &__btn{
      margin-top: 1.875em;
      height: 2.75em;
    }
  }

  .process{
    .video{
      margin-top: 1.25em;
      &__img{
        img{
          min-height: 13em;
          object-fit: cover;
        }
      }
    }
    &__text{
      margin-top: 1.25em;
      p{
        font-size: 1em;
      }
      ul li{
        line-height: 120%;
        &:before{
          top: 0;
        }
      }
    }
  }

  .assortment{
    padding: 2.5em 0;
    margin: 1.875em 0;
    min-height: unset;
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: repeat(4,calc(25% - 7.5em / 4));
      gap: 2.5em;
    }
  }

  .assortment-item{
    &__num{
      font-size: 2.25em;
    }
    &__text{
      min-height: unset;
    }
    &__btn{
      opacity: 1;
    }
  }

  .opinion{
    &__top{
      gap: .625em;
    }
    &__items{
      margin-top: 1.25em;
    }
  }

  .site-item{
    &__img{
      height: 1em;
    }
  }

  .rating-block{
    &__num{
      font-size: 1.1875em;
    }
  }

  .rating{
    &__fill img,
    &__stars{
      width: 4.625em;
    }
  }

  .assortment-projects{
    margin-top: 2.5em;
  }

  .assortment-item2{
    &__title{
      font-size: 2.25em;
    }
    &__desc{
      margin-top: .5em;
    }
    &__projects{
      margin-top: 2.5em;
      gap: .75em;
    }
    &__project{
      font-size: .9375em;
    }
  }
}


@media screen and (max-width: 576px){
  .process{
    &__text{
      @include grid100gap(1.25em);
    }
  }

  .assortment{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .opinion{
    &__items{
      padding: 0 1.5em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 2.75em 1em;
    }
  }

  .assortment-projects{
    grid-template-columns: 100%;
  }
}
