.profile{
  &__top{
    position: relative;
    .user-data__change{
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
    }
  }
}

.profile-type{
  display: flex;
  gap: 0;
  margin-top: 1.875em;
  .radio-field{
    margin-right: .8125em;
    background-color: transparent;
    border: 1px solid #E9E9E9;
  }
}

.user-data{
  margin-top: 1.875em;
  padding: 1.875em 1.25em;
  box-shadow: 0 .25em .625em rgba(0, 0, 0, 0.15);
  border-radius: .625em;
  position: relative;
  &__change{
    position: absolute;
    display: flex;
    align-items: center;
    color: $grayDark;
    top: 1.875em;
    right: 1.25em;
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    svg{
      width: 1em;
      margin-right: .625em;
    }
    span{
      font-size: .875em;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 122%;
    font-weight: 700;
    letter-spacing: 0.01em;
  }
  &__props{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: 16.875em calc(100% - 18.75em);
    gap: 1.875em;
    &._type2{
      @media screen and (min-width: 993px){
        grid-template-columns: repeat(9,calc(100% / 9 - 1.875em * 8 / 9));
      }
    }
  }
  &__prop{
    &-title{
      font-size: .875em;
      color: $grayActive;
    }
    &-desc{
      font-size: 1em;
      line-height: 120%;
      margin-top: .25em;
    }
  }
  &__fields{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.25em / 4));
    gap: .75em;
  }
  &__btn{
    margin-top: 1.875em;
    width: 9.375em;
    border-radius: .5em;
    span{
      font-size: .875em;
    }
  }
}

.user-requisites{
  margin-top: 2.8125em;
  width: calc((25% - 2.25em / 4) * 2 + .75em);
  max-width: 100%;
  &__top{
    margin-bottom: .75em;
    @include grid100gap(.5em);
  }
  .requisites-form__fields{
    margin: 0;
  }
}

.user-address{
  margin-top: 1.875em;
  display: grid;
  grid-template-columns: 19.25em calc(100% - 20.5em);
  gap: 1.25em;
  &__col2{
    margin-top: -3.25em;
  }
  .cart-map{
    margin: 0;
  }
  .map{
    height: 30.75em;
  }
}

.address-form{
  &__fields{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
    gap: .75em .625em;
  }
}

.elevator-field{
  padding: 1em 1.25em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__checks{
    display: flex;
    align-items: center;
    .m-check{
      margin-right: 1.25em;
      label{
        min-height: 1.5em;
        display: flex;
        align-items: center;
        padding-top: 0;
      }
      &:last-child{
        margin-right: 0;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .profile{
    .breadcrumbs{
      display: none;
    }
    .delivery__links {
      width: 100%;
      a{
        padding: 0 .125em;
      }
    }
    &__top{
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      .user-data__change{
        position: static;
        transform: unset;
        margin: .625em 0 0;
      }
    }
  }

  .profile-type{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    .radio-field{
      margin: 0;
    }
  }

  .user-data{
    &__props{
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 1.875em;
    }
    &__fields{
      grid-template-columns: repeat(2,calc(50% - .375em));
    }
    &__btn{
      margin-top: 1.25em;
      width: 100%;
    }
  }

  .user-requisites{
    margin-top: 1.25em;
    width: 100%;
    &__top{
      gap: 1em;
      margin-bottom: 1.125em;
    }
  }

  .user-address{
    grid-template-columns: 100%;
    gap: .75em;
    position: relative;
    padding-top: 3.375em;
    &__col2{
      margin: 0;
      order: -1;
    }
  }

  .address-form{
    &__city{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .profile-type{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .125em));
    gap: .25em;
    text-align: center;
  }

  .user-data{
    &__props{
      grid-template-columns: 100%;
    }
    &__change{
      position: static;
      margin: 0 0 1.25em auto;
    }
    &__fields{
      grid-template-columns: 100%;
      gap: .625em;
    }
  }

  .user-address{
    .map{
      height: 15em;
    }
  }

  .address-form{
    &__fields{
      gap: .625em;
    }
  }

  .elevator-field{
    padding: 1em 0;
  }
}
