.catalog{
  &__count{
    margin-top: .25em;
    color: $grayDark;
  }
  &__grid{
    display: grid;
    grid-template-columns: 18.75em calc(100% - 18.75em - 2.875em);
    justify-content: space-between;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.875em 1.5em;
  }
  .product-card__cart-btn{
    opacity: 1;
    pointer-events: unset;
    background-color: #BCBCBC;
  }
}

.m-dropdown{
  position: relative;
  &__show{
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 2em;
    color: $grayDark;
    transition: all .3s;
    &:after{
      content: '';
      flex-shrink: 0;
      width: .625em;
      height: .625em;
      background-image: url(../img/seect-arrow.svg);
      @include bgImgSize(100%);
      margin-left: .625em;
      transition: all .3s;
    }
  }
  &__hidden{
    border-radius: .625em;
    position: absolute;
    top: 100%;
    margin-top: .5em;
    left: -1.25em;
    padding: 1.5em;
    background-color: $white;
    box-shadow: $shadow;
    z-index: 6;
    min-width: calc(100% + 2.5em);
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__options{
    @include grid100gap(1.125em);
  }
  &__clear{
    margin-top: 1em;
    text-decoration: underline;
    color: $grayDark;
    display: none;
    span{
      font-size: .875em;
    }
    &:hover{
      color: $primary;
    }
  }
  &__search{
    margin-bottom: 1em;
    input{
      padding-left: 1.67em;
      border-radius: .67em;
      height: 2.67em;
      font-size: .75em;
    }
  }
  &._open{
    z-index: 10;
    .m-dropdown__show:after{
      transform: rotate(-180deg);
    }
    .m-dropdown__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.sort{
  .m-radio{
    white-space: nowrap;
  }
  &._selected{
    .m-dropdown__show{
      color: $primary;
    }
  }
}

.catalog-view{
  display: flex;
  &__tab{
    height: 1.4375em;
    margin-left: 1.5em;
    svg{
      height: 100%;
      width: auto;
    }
    &:hover{
      cursor: pointer;
      rect{
        fill: $black;
      }
    }
    &._active{
      pointer-events: none;
      rect{
        fill: $black;
      }
    }
  }
}

.filter{
  margin-top: 1em;
  display: flex;
  flex-wrap: wrap;
  &__all{
    margin: .375em .375em 0 0;
    label{
      display: flex;
      align-items: center;
      padding: 0 .625em;
      height: 1.75em;
      border-radius: .625em;
      background-color: $gray;
      color: $grayDark;
    }
    input:checked{
      &~label{
        background-color: $grayDark;
        color: $white;
      }
    }
    span{
      font-size: .875em;
    }
  }
  &__more{
    text-decoration: underline;
    color: $grayDark;
    margin-left: auto;
    margin-top: .375em;
    &:hover{
      color: $primary;
    }
    span{
      font-size: .875em;
    }
  }
}

