      }
    }
    .m-dropdown__clear{
      display: block;
    }
  }
}

.mob-filter{
  &__btn{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    .h1{
      text-align: left;
    }
    &__count{
      display: none;
    }
    &__top{
      margin: 0;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__items{
      position: relative;
      z-index: 2;
    }
  }

  .catalog-view{
    display: none;
  }

  .mob-filter{
    position: relative;
    padding-bottom: 2.5em;
    margin-top: 1.25em;
    &__btn{
      width: 1.5em;
      @include center;
      position: absolute;
      right: 0;
      bottom: 0;
      img{
        width: 100%;
        display: block;
      }
    }
  }

  .filter{
    margin: -.375em 0 -15em -.625em;
    width: calc(100% + 1.25em);
    flex-wrap: nowrap;
    overflow-x: auto;
    padding: 0 .625em 15em;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    .m-dropdown{
      &__hidden{
        font-size: .875em;
      }
    }
    &__more{
      margin: 0;
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      height: 1.5em;
      @include center;
    }
  }

  .filter-item{
    flex-shrink: 0;
  }

  .sort{
    position: absolute;
    left: 0;
    bottom: 0;
    .m-dropdown{
      &__show{
        width: 1.5em;
        height: 1.5em;
        background-image: url(../img/sort.svg);
        @include bgImgSize(100%);
      }
      &__current,
      &__show:after{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .1875em));
      gap: .625em .375em;
    }
    .product-card{
      &__img{
        padding-bottom: 91.5%;
      }
      &__content{
        padding: .625em .5em;
      }
      &__prices{
        display: block;
      }
      &__price2{
        margin: 0;
      }
    }
  }
}
