.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background: rgba(0, 0, 0, 0.3);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 25.125em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 1.875em 2.5em;
        border-radius: 1.25em;
        display: inline-block;
    }
    &__title{
        text-align: center;
        text-transform: unset;
        margin-bottom: .77em;
    }
    .close-btn{
        position: absolute;
        top: -1.5625em;
        right: -1.5625em;
    }
}

.close-btn{
    width: 3.125em;
    height: 3.125em;
    border-radius: 50%;
    @include center;
    flex-shrink: 0;
    img{
        width: 1.375em;
    }
}

.modal-form{
    &__hint{
        padding: 1.25em 1em 0;
        a{
            text-decoration: underline;
        }
    }
    &__btn{
        width: 100%;
        margin-top: 1.25em;
        border-radius: .5em;
        height: 2.5em;
        span{
            font-size: .875em;
        }
    }
    &__fields{
        margin-top: 2.5em;
        @include grid100gap(.875em);
    }
}

.fg-phone{
    input{
        height: 2.875em;
        font-size: 1em;
        padding-left: 5.75em;
        &::placeholder {
            color: $grayActive;
        }
    }
}

.code-modal{
    .modal-form__btn{
        width: 12em;
        margin-left: auto;
        margin-right: auto;
    }
    .code-field{
        justify-content: center;
    }
    &__nav{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: .625em;
    }
    &__btn{
        color: $black;
    }
    &__hint{
        text-align: center;
    }
}

.code-field{
    display: flex;
    .input-group{
        margin-right: .625em;
        display: flex;
        &:last-child{
            margin-right: 0;
        }
        input{
            font-size: 1.5em;
            height: 1.91em;
            width: 1.71em;
            text-align: center;
            padding: 0;
            margin-right: .42em;
            border-radius: .33em;
            &:last-child{
                margin-right: 0;
            }
        }
    }
    &__wrap{
        &._error{
            iput{
                border-color: $primary;
            }
        }
    }
    &__error{
        color: $primary;
        margin-top: 1.25em;
        text-align: center;
    }
}

.reg-modal{
    .code-field{
        justify-content: center;
    }
    .code-modal__nav{
        margin-top: 1.25em;
    }
    .code-modal__hint{
        margin-bottom: .75em;
    }
    .m-check{
        margin-top: 2.5em;
        label{
            color: $grayActive;
            padding-top: 0;
            span{
                font-size: .75em;
            }
        }
    }
    .modal-form__btn{
        width: 12em;
        margin: 1.875em auto 0;
    }
    &__hint{
        padding: 1.875em 1em 0;
        color: $grayActive;
    }
}


@media screen and (max-width: 992px){
    .modal{
        padding-top: 3em;
        &__dialog {

        }
        &__title{
            font-size: 1em;
        }
        .close-btn{
            right: 0;
            top: unset;
            bottom: 100%;
            margin-bottom: .6875em;
        }
    }

    .close-btn{
        width: 1.875em;
        height: 1.875em;
        img{
            width: .875em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding: 1.875em 1em;
        }
    }

    .code-modal {
        &__nav,
        .modal-form__btn {
            margin-top: .625em;
        }
        &__hint,
        &__btn{
            font-size: .75em;
        }
    }

    .modal-form{
        &__fields{
            gap: .75em;
            margin-top: 1.5em;
        }
    }

    .reg-modal{
        .code-modal__hint{
            margin-bottom: .25em;
        }
        .modal-form__btn{
            width: 100%;
            margin-top: 1.75em;
        }
        &__hint{
            padding: 1.125em 0 0;
        }
    }

}
