.catalog-item{
  &__top{
    margin-top: .5em;
  }
  .product-card__feedback{
    margin-left: 4.875em;
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: 48.875em calc(100% - 48.875em - 3.75em);
    justify-content: space-between;
  }
  .gallery{
    &__img{
      padding-bottom: 58.82%;
    }
    &__small{
      margin-top: 1.25em;
      .gallery__img{
        padding-bottom: 59.4%;
      }
      .swiper{
        width: calc(100% + 1.375em);
        &-slide{
          padding-right: 1.375em;
          width: 20%;
        }
      }
    }
    .swiper-button{
      background-color: $white;
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      path{
        fill: $black;
      }
      &-prev{
        left: .625em;
      }
      &-next{
        right: .625em;
      }
    }
  }
  &__available{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    color: $green;
    img{
      width: 1.25em;
      margin-right: .0625em;
    }
  }
  &__prices{
    margin-top: -1.75em;
    display: flex;
    &-item,
    &-item2{
      font-size: 1.5em;
      letter-spacing: 0.01em;
      line-height: 120%;
    }
    &-item2{
      text-decoration: line-through;
      color: $grayActive;
    }
    &-sale{
      margin-left: .9375em;
      border-radius: .3125em;
    }
  }
  &__nav{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__btn{
    height: 3em;
    border-radius: .5em;
    span{
      font-size: 1.125em;
    }
    ._view2{
      display: none;
    }
  }
}

.timer{
  margin-top: 1.875em;
  padding: .9375em;
  background-color: $gray;
  border-radius: .625em;
  text-align: center;
  @include center;
  flex-direction: column;
  .countdown{
    margin-top: 1.25em;
  }
}

.countdown{
  display: flex;
}

.countdownProp{
  display: flex;
  &__item{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__num,
  ._dots{
    font-size: 1.5em;
    line-height: 120%;
    font-weight: 700;
  }
  ._dots{
    margin: 0 1.125em;
  }
  &__title{
    font-size: .75em;
    line-height: 120%;
    margin-top: .45em;
  }
}

.product-colors{
  margin-top: 1.875em;
  &__tabs{
    display: flex;
  }
  &__tab{
    margin-right: .25em;
    padding: 0 .6875em;
    display: flex;
    align-items: center;
    height: 1.75em;
    color: $grayDark;
    transition: all .3s;
    cursor: pointer;
    border-radius: .625em .625em 0 0;
    border: 1px solid transparent;
    border-bottom: none;
    span{
      font-size: .875em;
      line-height: 120%;
    }
    &:hover{
      color: $black;
    }
    &._active{
      pointer-events: none;
      border-color: $black;
      color: $black;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__grid{
      position: relative;
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 1.25em;
      @media screen and (min-width: 577px){
        grid-template-columns: 22em calc(100% - 23.25em);
      }
    }
    &__head{
      @include grid100gap(1em);
    }
    &__top{
      order: -1;
      margin: 0;
      display: block;
    }
    .product-card__feedback{
      margin: .25em 0 0;
    }
    .gallery{
      &__small{
        margin-top: .625em;
        .swiper{
          width: calc(100% + .3125em);
          &-slide{
            padding-right: .3125em;
          }
        }
        .gallery__img{
          border-radius: .25em;
        }
      }
      &__img{
        border-radius: .5em;
      }
      .swiper-button,
      &__action,
      &__badges{
        display: none;
      }
    }
    &__available{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 3.375em;
    }
    &__prices{
      margin: 0;
      &-items{
        display: flex;
      }
      &-item,
      &-item2{
        font-size: 1.1875em;
      }
      &-item2{
        margin-left: 1.1875em;
      }
      &-sale{
        margin-left: .75em;
      }
    }
    &__nav{
      grid-template-columns: repeat(2,calc(50% - .1875em));
      gap: .375em;
    }
    &__btn{
      height: 2.75em;
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
      span{
        font-size: .9375em;
      }
    }
  }

  .timer{
    margin-top: 1.25em;
    padding: .75em;
    border-radius: .4375em;
    &__title{
      font-size: .8125em;
    }
    .countdown{
      margin-top: .5em;
    }
  }

  .countdownProp{
    &__num,
    ._dots{
      font-size: 1.125em;
    }
    &__title{
      font-size: .5em;
      margin-top: -.5em;
    }
  }

  .product-colors{
    &__tab{
      padding: 0 .5em;
      margin-right: 0;
      span{
        font-size: .8125em;
      }
    }
  }
}


