.blog-card,
.blog-card2{
  background-color: $gray;
  border-radius: .625em;
  transition: all .3s;
  &__date{
    text-align: right;
    font-size: .75em;
  }
  &__title{
    font-size: .875em;
    font-weight: 700;
    height: 2em;
    @include truncate(2);
  }
  &__desc{
    font-size: .875em;
    @include truncate(3);
  }
}

.blog-card{
  padding: .9375em .625em 1.25em;
  &:hover{
    background-color: #fff;
    box-shadow: $shadow;
    .blog-card__title{
      color: $primary;
    }
  }
  &__img{
    @include imgCover(84.7%);
    border-radius: 1.25em;
  }
  &__top{
    @include grid100gap(.3125em);
    padding: 0 .625em .625em;
  }
  &__bottom{
    padding: .625em .625em 0;
  }
}

.blog-item{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 21.875em - 1.25em) 21.875em;
    gap: 1.25em;
  }
  &__col2{
    margin-top: -1.875em;
  }
  &__date{
    margin-top: .5em;
  }
  .text{
    p,img{
      @include lastM(0 0 1.875em);
    }
  }
}

.last-news{
  position: sticky;
  top: 1em;
  &__items{
    @include grid100gap(.9375em);
  }
}

.blog-card2{
  padding: .625em;
  &__grid{
    display: grid;
    grid-template-columns: 6.25em calc(100% - 6.875em);
    gap: .625em;
    margin-top: .625em;
  }
  &__img{
    @include imgCover(80%);
    border-radius: .625em;
  }
  &__title{
    height: unset;
    margin-bottom: .5em;
  }
}

.blog-gallery{
  @include lastM(0 0 1.875em);
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - .125em));
  gap: .1875em;
  &__img{
    @include imgCover(89%);
    border-radius: 1.25em;
  }
}

.article-bottom{
  padding: 1.875em 0;
  margin-top: 3.75em;
  border-top: $border;
  border-bottom: $border;
  .socials{
    justify-content: center;
  }
}

.article-nav{
  display: flex;
  justify-content: space-between;
  margin-top: .625em;
  &__link{
    display: flex;
    align-items: flex-start;
    width: 48%;
    svg{
      width: 1.5em;
      margin-right: .3125em;
    }
    &-content{
      padding-top: .25em;
      @include grid100gap(.3125em);
    }
    &._next{
      text-align: right;
      svg{
        margin: 0 0 0 .3125em;
      }
    }
  }
}

.comment-form{
  margin-top: 3.75em;
  &__top{
    @include grid100gap(.625em);
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.75em / 3));
    gap: .875em;
  }
  &__hint{
    max-width: 22.25em;
    margin-left: 1.25em;
    color: $grayActive;
  }
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 992px){
  .blog{
    .breadcrumbs{
      display: none;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1.25em;
    }
    .pagination{
      margin-top: 3.75em;
    }
  }

  .blog-item{
    .breadcrumbs{
      display: none;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 3.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .blog{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .blog-item{
    .h1,
    &__date{
      text-align: center;
    }
    &__date{
      margin-top: 1.9em;
      font-size: .6875em;
    }
    .text{
      img{
        @include lastM(0 0 .6875em);
      }
      p{
        font-size: .6875em;
        @include lastM(0 0 1em);
      }
    }
  }

  .blog-gallery{
    @include lastM(0 0 .6875em);
    &__img{
      border-radius: .625em;
    }
  }

  .article-bottom{
    margin-top: 2.5em;
  }

  .article-nav{
    &__link{
      width: calc(50% - 1em);
      &-title{
        font-size: .6875em;
      }
      &-desc{
        font-size: .625em;
      }
    }
  }

  .comment-form{
    &__grid{
      grid-template-columns: 100%;
      gap: .8125em;
      margin-top: .75em;
    }
    &__bottom{
      margin-top: .75em;
      @include grid100gap(1.25em);
    }
    &__hint{
      margin: 0;
    }
  }
}
